@font-face {
  font-family: 'Work Sans';
  src: url('./Assets/Fonts/WorkSans-Bold.eot'); /* IE9 Compat Modes */
  src: url('./Assets/Fonts/WorkSans-Bold.woff') format('woff'), /* Modern Browsers */
       url('./Assets/Fonts/WorkSans-Bold.ttf')  format('truetype'), /* Safari, Android, iOS */
       url('./Assets/Fonts/WorkSans-Bold.woff2') format('woff2'),
       url('./Assets/Fonts/WorkSans-Bold.svg#WorkSans-Bold') format('svg');
  font-weight: bolder;
  font-style: normal;
  font-display: swap;
}

